<template>
    <view class="integral_gift_points_info_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">馈赠分详情</block></cu-custom>
        <!-- 头部 -->
        <view class="things_width_full flex justify-start align-start integral_gift_points_info_page_top">
            <!-- 余额 -->
            <view class="flex justify-center align-start balance">
                <view class="balance_text">馈赠分</view>
                <view class="balance_value">{{userAmount.gift_points}}</view>
            </view>
            <!-- 积分规则 -->
            <view class="rule_info">
                <view class="rule_text">积分规则</view>
                <view class="flex justify-end align-center btn_box">
                    <button class="cu-btn" :disabled="buttonType" @tap="linkToRechargeIntegral">充值</button>
                    <button class="cu-btn" :disabled="buttonType" @tap="linkToGiveAway">赠送</button>
                </view>
            </view>
        </view>
        <!-- 列表 -->
        <view class="gift_points_info_list">
            <view class="flex justify-center align-center gift_points_info_list_title">积分明细</view>
            <scroll-view v-if="giftPointsList.length > 0" class="gift_points_list" scroll-y @scrolltolower="getMoreData">
                <view v-for="(item,index) in giftPointsList" :key="index" class="flex justify-between align-center gift_points_item">
                    <view class="gift_points_item_left">
                        <view class="gift_points_item_from">{{item.type_text}}</view>
                        <view class="gift_points_item_time">{{item.create_time}}</view>
                    </view>
                    <view class="gift_points_item_right">{{item.value}}</view>
                </view>
                <view v-if="!pageMore" class="flex justify-center align-center not_more">暂无更多</view>
            </scroll-view>
            <view v-else class="flex justify-center align-center kong_data_box">
                <image class="kong_image" src="./../../../../../static/images/global/kong.png" mode="widthFix" />
                <view class="kong_text">暂无数据</view>
            </view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { giftsPointsFraction_api,giftPointsList_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
let that = null;
export default {
    data() {
        return {
            userData: storage.get("userData") || {},
            // 列表数据
            page: 1,// 页数
            page_size: 10,// 每页个数
            pageMore: true,// 列表状态，false:无更多数据,true:有更多数据
            userAmount: {},// 积分分数
            giftPointsList: [],
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.user_userData': function(val) {
            that.userData = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'user'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        clearPageFun(e) {
            that.page = 1;
            that.pageMore = true;
            that.giftPointsList = [];
            that.getFraction();
            that.getGiftPointsList();
        },
        /** 跳转到赠送页面
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToGiveAway(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/integral/giftPoints/giveAway/index`,
                complete: function() {
                    that.buttonType = false;
                }
            });
        },
        /** 跳转到充值馈赠分页面
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToRechargeIntegral(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/amount/recharge/integral/index`,
                complete: function() {
                    that.buttonType = false;
                }
            });
        },
        /**获取馈赠分列表
         * @description: 添加数据、判断是否有更多数据
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getGiftPointsList() {
            let params = {
                page: that.page,
                page_size: that.page_size,
                type: 20,
            };
            giftPointsList_api(params,that).then(res=>{
                console.log(res);
                that.giftPointsList = that.giftPointsList.concat(res.list);
                if (res.total > that.giftPointsList.length) {
                    that.pageMore = true;
                    that.page++;
                }else{
                    that.pageMore = false;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        /**获取更多数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getMoreData(e) {
            if (that.pageMore) that.getGiftPointsList();
        },
        /**获取积分分数
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFraction(e) {
            let params = {};
            giftsPointsFraction_api(params,that).then(res=>{
                that.userAmount = res.userAmount;
            }).catch(err=>{
                console.log(err);
            })

        },
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
    },
    onShow: function() {
        that.clearPageFun();
    }
}
</script>

<style lang="scss" scoped>
    @import './index';
</style>